<template>
  <div class="pagination">
    <button @click='getNumber'><<</button>
    <button v-if='currentPage>=5'>...</button>
    <button v-for='item in array'
            @click='getNumber(item)'
            :class='[{currentPage: item==currentPage},"pagebtn"]'
            >{{item}}
    </button>
    <button>...</button>
    <button @click='getNumber'>>></button>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'pagination',
  data(){
    return {
      currentPage: 1,
      array: [1,2,3,4,5]
    }
  },
  methods: {
    getNumber: function(item){
//       上一页，下一页
      if(typeof item != 'number'){
        switch(item.target.innerText){
          case '<<':
            $('button.currentPage').prev().click()
            break
          case '>>':
            $('button.currentPage').next().click()
            break
        }
        return
      }
      
      this.currentPage = item
//       如果按第四个，将数组变大
      if(item == this.array[4] ){
        this.array.shift()
        this.array.push(this.array[3]+1)
      }else if(item == this.array[0] && item != 1){ //按第一个，将数组减小
        this.array.pop()
        this.array.unshift(this.array[0]-1)
      }
      this.$emit('handleList',this.currentPage)
    }
  }
}
</script>

<style scoped>
 .pagination {
    margin-top: 5px;
    margin-bottom: 20px;
    background-color: white;
    padding: 6px 20px;
    border-radius: 5px;
    /*box-shadow: 0px 2px 9px #888888;*/
    border: 1px solid #888888;
  }

  button {
    background-color: #fff;
    border: 1px solid #ddd;
    color: #778087;
    border-radius: 3px;
    outline: none;
    height: 21px;
    cursor: pointer;
    padding: 0 2px;
    width: 55px;
    height: 29px;
  }

  .item {
    position: relative;
    bottom: 1px;
    width: 40px;
    margin: 0 4px;
  }

  .currentPage {
    color: white;
    background-color: #1f1b1b;

  }
</style>



