<template>
  <div class="wrapper">
    <ul>
      <li><span>全部</span><span>精华</span><span>分享</span><span>问答</span><span>招聘</span><span>客户端测试</span></li>
      <li v-for="post in posts">
        <img :src="post.author.avatar_url">
        <span class="count">
          {{post.reply_count}}/<span class="visit_reply">{{post.visit_count}}</span>
        </span>
        <!-- 状态 -->
        <span
        v-bind:class="[{good:post.good==true},
        {top:post.top==true},
        {others:post.good==false&&post.top==false}]"
        >
          {{post | tabformate}}
        </span>
        <!-- 标题 -->
        <span class="title">
        <router-link 
        :to="{name:'article',params:{
          id: post.id,
          name: post.author.loginname
        }}"
        >
          {{post.title}}
        </router-link>
        </span>
        <!-- 时间 -->
        <span class="time">
          {{post.last_reply_at | formateDate}}
        </span>
      </li>
    </ul>
    <!-- 子组件给父组件传递数据 -->
    <pagination @handleList='renderList'></pagination>
  </div>
</template>

<script>
import pagination from './pagination'
export default {
  name: 'PostList',
  data(){
    return {
      posts:[],
      postpage:1
    }
  },
  components: {
    pagination
  },
  methods:{
    getData(){
      this.$http.get('https://cnodejs.org/api/v1/topics',{
      params:{
        page:this.postpage,
        limit:20
      }
      })
      .then(res=>{
        this.posts = res.data.data
      })
      .catch(err=>{
        console.log(err)
      })
    },
    renderList(value){
      this.postpage = value
      this.getData()
    }
  },
  beforeMount(){
    this.getData()
    }
  }
</script>

<style scoped>
.wrapper{
  width: 80%;
  margin: 18px auto;
  background: white;
  border:none;
}
img{
  width: 30px;
  padding: 5px;
}
.top,
.good,
.others{
  font-size: 12px;
  border-radius: 2px;
  padding: 0 3px;
}
.top,
.good{
  color:white;
  background: #80bd01;
}
.others{
  color:#333;
  background: #e5e5e5;
}
ul{
  list-style: none;
}
ul>li:nth-child(1){
  color: #80bd01;
  background: #f6f6f6;
  padding-left:10px;
  font-size: 14px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}
ul>li:nth-child(1)>span{
  padding-right: 20px;
}
li{
  display: flex;
  border-bottom: 1px solid #9e78c0;
  padding: 10px 0;
  align-items: center;
}
.count{
  font-size: 14px;
  color: #9e78c0;
  padding-right:10px;
}
.visit_reply{
  font-size: 13px;  
  color: #b4b4b4
}
.title{
  font-size: 16px;
  padding-left: 6px;
}
.time{
  font-size: 12px;
  margin-left: auto;
  margin-right: 10px;
}
</style>





