// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'

Vue.prototype.$http = axios
Vue.config.productionTip = false

//过滤器
Vue.filter('formateDate', (value) => {
    let lastDate = new Date(value)
    let newDate = new Date()
    let time = (Date.parse(newDate) - Date.parse(lastDate)) / 1000

    if (time < 60) {
        return '刚刚'
    } else if (time < 3600) {
        return parseInt(time / 60) + '分钟前'
    } else if (time < 86488) {
        return parseInt(time / 3600) + '小时前'
    } else if (time < 2678400) {
        return parseInt(time / 86488) + '天前'
    } else if (time < 32140800) {
        return parseInt(time / 2678400) + '月前'
    } else {
        return parseInt(time / 32140800) + '年前'
    }
})

Vue.filter('tabformate', function(value) {
    if (value.good) {
        if (value.top) {
            return '置顶'
        } else {
            return '精华'
        }
    } else if (value.top) {
        return '置顶'
    } else if (value.tab == 'share') {
        return '分享'
    } else if (value.tab = 'ask') {
        return '问答'
    } else {
        return '招聘'
    }
})



/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


