<template>
  <div>
    <div class="wrapper">
    <p class="title">作者</p>
    <router-link :to="{name: 'userinfo',params:{
      name: post.loginname
    }}">
      <img :src='post.avatar_url'>    
    </router-link>
    <span class="name">{{post.loginname}}</span>
    <p>积分：{{post.score}}</p>
    <ul>
      <p class="title">最近主题</p>
        <li v-for="item in topics">
      <img :src='post.avatar_url'>                   
      <router-link :to='{name: "article",
      params:{
        id: item.id
      }
      }'>
          {{item.title}}
      </router-link>
      <span class="time">{{item.last_reply_at | formateDate}}</span>          
        </li>        
      <p class="title">最近回复</p>
        <li v-for="item in replies">
      <img :src='post.avatar_url'>                             
      <router-link :to='{name: "article",
      params:{
        id: item.id
      }
      }'>        
          {{item.title}}
      </router-link>
      <span class="time">{{item.last_reply_at | formateDate}}</span>                                
        </li>
    </ul>
  </div>
  </div>
</template>
<script>
export default {
  name: 'userinfo',
  data(){
    return {
      post: []
    }
  },
   beforeMount(){
    this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
    .then(res=>{
      this.post = res.data.data
    })
    .catch(err=>{
      console.log(err)
      })
    },
    computed:{
      topics(){
        if(this.post.recent_topics){
          return this.post.recent_topics.slice(0,5)
        }
      },
      replies(){
        if(this.post.recent_replies){
          return this.post.recent_replies.slice(0,5)
        }
      }
    }
}
</script>

<style scoped>
.wrapper{
  width: 80%;
  margin: 0 auto;
  background: white;
}
.title{
  margin-bottom: 10px;
  font-size: 13px;
  color: black;
  background: #f6f6f6;
  padding: 8px;
}
.time{
  display: inline-block;
  float: right;
}
ul{
  list-style: none;
}
li{
  padding: 6px 10px;
  border-bottom: 1px solid #e1e1e1;
}
img{
  width: 30px;
}
.name{
  line-height: 30px;
}
</style>



