<template>
  <div class="wrapper">
    <div class="header">
      <!-- 状态 -->
        <span
        v-bind:class="[{good:post.good==true},
        {top:post.top==true},
        {others:post.good==false&&post.top==false}]"
        >
          {{post | tabformate}}
        </span>
        <!-- 标题 -->
        <span class="title">{{post.title}}</span>
        <!-- 信息 -->
        <ul class="message">
          <li>·发布于{{post.create_at | formateDate}}</li>
          <li>·作者{{post.author.loginname}}</li>
          <li>·{{post.visit_count}}次浏览</li>
          <!-- bug.需要过滤器 -->
          <li>·来自{{post | tabformate}}</li>
        </ul>
    </div>
    <div class="content" v-html="post.content"></div>
    <!-- 回复区 -->
    <ul class="reply">
      <li v-for="(item,index) in post.replies">
        <img :src="item.author.avatar_url">
        <span>{{item.author.loginname}}</span>
        <span>{{index+1}}楼</span>
        <span>{{item.create_at | formateDate}}</span>
        <p v-html="item.content"></p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Article',
  data(){
      return {
        post:[]
      }
    },
  beforeMount(){
    this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
    .then(res=>{
      this.post = res.data.data
    })
    .catch(err=>{
      console.log(err)
      })
    },
    watch:{
      '$route'(to,from){
        this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
      .then(res=>{
        this.post = res.data.data
      })
      .catch(err=>{
        console.log(err)
       })
     },
    }
}
</script>

<style scoped>
@import url('../assets/markdown-github.css');
.wrapper{
  margin-left: 5%;
  margin-top: 10px;
  margin-right: 340px;
  background: white;
}
.header{
  border-bottom: 1px solid #333;
  margin-left: 10px;
}
.title{
  font-weight: bold;
  font-size: 22px;
}
.good,
.top{
  color: white;
  display: inline-block;
  font-size: 12px;
  background: #80bd01;
  border-radius: 4px;
}
img{
  width: 30px;
  height: 30px;
}
ul{
  list-style: none;
}
ul.message{
  display: flex;
  font-size: 12px;
  margin: 10px 5px;
  color: #333;
}
ul.message>li{
  padding-right: 12px;
}
.content{
  margin: 10px 20px;
  padding: 0 10px;
}

.reply{
  border-top: 10px solid #e1e1e1;
}
.reply>li{
  border: 1px solid #e1e1e1;
  padding-top:30px; 
  padding-bottom: 50px;
}
.reply>li>span{
  font-size: 12px;
  padding: 0 5px;
}
.reply>li>p{
  align-self:center;
  padding-top: 10px;
  left: 50px;
  top:50px;
}

</style>





